import React from "react"
import '../scss/main.scss';
import Sidebar from '../components/Sidebar';
import SidebarButton from '../components/SidebarButton';
import Link from 'gatsby-link';

const logoStyle = {
  width: '100px',
  border: '0',
  boxShadow: 'none',
  marginBottom: '0'
}

const docDetails = {
  version: '1.02',
  issued: '04 January 2019',
  author: 'Chargebacks911',
  updateDate: '10 January 2019'
};

export default () => (
  <div className="content">
    <SidebarButton />
    <Sidebar />
    <div className="container">
      <div className="row">
        <div className="col-12">
          <div className="center u-m-top--lg u-bg--primary u-text--white u-p-left--lg u-p-right--lg u-p-top--lg u-p-bottom--lg u-m-bottom--lg">
            <Link to="./">
              <img alt="DisputeLab" src="img/disputelab_app-icon.svg" style={logoStyle} width="80px"/>
            </Link>
            <h1>Admin Guide</h1>
            <p>Comprehensive guides that may help you better understand the various functionalities, features, and actions of DisputeLab &mdash;  a management portal developed for acquirers.</p>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--success">
            <Link className="u-text--dark" to="/introduction">
              <strong>Introduction</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--warning">
            <Link className="u-text--dark" to="/dashboard">
              <strong>Dashboard</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--danger">
            <Link className="u-text--dark" to="/case-admin">
              <strong>Case Admin</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--info">
            <Link className="u-text--dark" to="/introduction">
              <strong>Merchant Admin</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--success">
            <Link className="u-text--dark" to="/introduction">
              <strong>Case Work</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--warning">
            <Link className="u-text--dark" to="/introduction">
              <strong>Rules</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--danger">
            <Link className="u-text--dark" to="/introduction">
              <strong>Templates</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--info">
            <Link className="u-text--dark" to="/introduction">
              <strong>Reports</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--success">
            <Link className="u-text--dark" to="/introduction">
              <strong>Unmatched Docs</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--warning">
            <Link className="u-text--dark" to="/introduction">
              <strong>ERT Notifications</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--danger">
            <Link className="u-text--dark" to="/introduction">
              <strong>Users</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-4">
          <div className="tile u-m-top tile--info">
            <Link className="u-text--dark" to="/introduction">
              <strong>Settings</strong>
              <div className="tile__right">
                <small>view &rarr;</small>
              </div>
            </Link>
          </div>
        </div>

        <div className="col-12">
          <div className="tile u-m-top--lg">
            <small>
              <table>
                <tbody>
                  <tr>
                    <td className="u-p-right u-text--secondary">Version:</td>
                    <td>{docDetails.version}</td>
                  </tr>
                  <tr>
                    <td className="u-p-right u-text--secondary">Issued On:</td>
                    <td>{docDetails.issued}</td>
                  </tr>
                  <tr>
                    <td className="u-p-right u-text--secondary">Author:</td>
                    <td>{docDetails.author}</td>
                  </tr>
                  <tr>
                    <td className="u-p-right u-text--secondary">Last Updated:</td>
                    <td>{docDetails.updateDate}</td>
                  </tr>
                </tbody>
              </table>
            </small>
          </div>
        </div>

      </div>
    </div>
  </div>
)
