import React from 'react';

function closeSideBar() {
  document.querySelector('.sidebar').classList.toggle('is-closed');
  document.querySelector('.sidebar__close').classList.toggle('is-closed');
}

const svgStyle = {
  margin: 'auto',
  display: 'block'
};

class SidebarButton extends React.Component {
  render() {
    return (
      <button class="sidebar__close is-closed" onClick={closeSideBar}>
        <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="auto" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true" style={svgStyle}>
          <line x1="5" y1="12" x2="19" y2="12"></line>
          <polyline points="12 5 19 12 12 19"></polyline>
        </svg>
      </button>
    )
  }
}

export default SidebarButton;
